.component-header {
  padding: 15px;
  position: relative;
  font-weight: normal;
  font-family: 'Libre Baskerville', serif;
  font-size: 32px;
  text-align: center;
  color: #333;
}

.component-header img {
  position: relative;
  top: 6px;
  padding: 0 14px;
  font-family: 'Libre Baskerville', serif;
}

.component-search-input {
  border-bottom: 1px solid #ccc;
}

.component-search-input > div {
  margin: 0 10px 10px 10px;
}

.component-search-input input {
  border-radius: 4px;
  border: 1px solid #bbb;
  box-sizing: border-box;
  font-size: 18px;
  padding: 10px 8px;
  width: 100%;
}

.component-emoji-result-row {
  border-bottom: 1px solid #ccc;
  padding: 10px;
  height: 32px;
  position: relative;

  cursor: pointer;
  font-family: 'Libre Baskerville', serif;
}

.component-emoji-result-row:hover {
  background-color: #eee;
  font-family: 'Libre Baskerville', serif;
}

.component-emoji-result-row img {
  width: 32px;
  height: 32px;
  padding-right: 10px;
  font-family: 'Libre Baskerville', serif;
}

.component-emoji-result-row .title {
  position: relative;
  top: -8px;
  font-family: 'Libre Baskerville', serif;
}

.component-emoji-result-row .info {
  float: right;
  position: relative;
  top: 8px;
  right: 10px;
  color: #ccc;
  display: none;
  font-family: 'Libre Baskerville', serif;
}

.component-emoji-result-row:hover .info {
  display: inline-block;
  font-family: 'Libre Baskerville', serif;
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Libre Baskerville', serif;
}

body .github-fork-ribbon:before {
  background-color: #333;
  font-family: 'Libre Baskerville', serif;
}

@media screen and (max-width: 500px) {
  .github-fork-ribbon {
    display: none;
    font-family: 'Libre Baskerville', serif;
  }
}

